.container {
    width: 1200px;
    margin: 0 auto;
}

.username {
    cursor: pointer;
}

#shortcut {
    box-sizing: border-box;
    font-size: 12px;
    border-bottom: 2px solid #ff6700;
    background: #fff;
    color: #b0b0b0;
    position: relative;
    z-index: 20;
    height: 100px;
    padding-top: 26px;

    a {
        position: relative;
        width: 48px;
        height: 48px;
        background-image: url(../img/logo.png);
        background-size: 100%;
        float: left;
    }

    .cart-title {
        float: left;
        line-height: 48px;
        margin-bottom: 0;
        font-size: 28px;
        font-weight: 400;
        color: #424242;
        margin-left: 40px;
    }

    p {
        float: left;
        height: 20px;
        line-height: 20px;
        margin-top: 20px;
        margin-left: 15px;
        color: #757575;
    }

    .username {
        float: right;
        font-size: 12px;
        margin-top: 20px;
    }

}

.Mycart {
    .Mycart-title {
        height: 70px;
        background-color: white;
        line-height: 70px;
        padding: 0 20px;
        border-bottom: #b0b0b0 1px solid;
        input{
            margin-right: 20px;
        }
        span:nth-child(2){
            margin-left: 200px;
        }
        span:nth-child(3){
            margin-left: 310px;
        }
        span:nth-child(4){
            margin-left: 115px;
        }
        span:nth-child(5){
            margin-left: 140px;
        }
        span:nth-child(6){
            margin-left: 105px;
        }
    }

    .cartList {
        margin-bottom: 20px;

        li {
            box-sizing: border-box;
            width: 100%;
            height: 117px;
            padding: 15px 26px 15px 0;
            background-color: white;
            border-bottom: #b0b0b0 1px solid;

            .check {
                vertical-align: middle;
                margin-top: 35px;
                margin-right: 40px;
                margin-left: 20px;
            }

            .item-img {
                width: 80px;
                height: 80px;
                margin-left: 20px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .item-name {
                margin-left: 50px;
                width: 425px;
            }

            .item-price {
                width: 115px;
                text-align: left;
            }

            .add,
            .sub {
                width: 38px;
                height: 38px;
                text-align: center;
                line-height: 38px;
                margin-top: 25px;
                border: 1px #e0e0e0 solid;
            }

            .item-num {
                margin-top: 25px;
                height: 38px;
                width: 72px;
                text-align: center;
                border-top: 1px #e0e0e0 solid;
                border-bottom: 1px #e0e0e0 solid;
                border-left: none;
                border-right: none;
            }

            .product-price {
                margin-left: 70px;
                color: #ff6700;
                width: 170px;
            }

            .delete {
                cursor: pointer;
            }
        }

        div {
            float: left;
            line-height: 87px;
        }

        input {
            float: left;
        }
    }

    .totalPrice {
        float: right;
        font-size: 25px;
        height: 50px;
        background-color: white;
        width: 1200px;
        color: #ff6700;
        position: sticky;
        bottom: 0px;
        line-height: 50px;
        box-shadow: 0 3px 4px rgb(0 0 0 / 18%);
        border: 1px solid #e0e0e0;

        .first {
            margin-left: 1000px;
        }
    }
}

footer {
    width: 100%;
    background-color: white;
    margin-top: 100px;

    .footer-service {
        padding: 27px 0;
        border-bottom: 1px solid #e0e0e0;

        ul {
            li {
                float: left;
                width: 19.8%;
                height: 25px;
                border-left: 1px solid #e0e0e0;
                font-size: 16px;
                line-height: 25px;
                text-align: center;

                &:nth-child(1) {
                    border: none;
                }

                a {
                    color: #616161;
                }

                .iconfont {
                    margin-right: 6px;
                    font-size: 24px;
                    line-height: 24px;
                    vertical-align: -4px;
                    color: #616161;
                }
            }

            li a:hover {
                color: #ff6700;
            }

            li :hover .iconfont {
                color: #ff6700;
            }
        }
    }

    .footer-links {
        padding: 40px 0;

        ul {
            float: left;
            width: 160px;

            h4 {
                margin: -1px 0 26px;
                font-size: 14px;
                line-height: 1.25;
                color: #424242;
                font-weight: 400;
            }

            li {
                margin: 10px 0 0;
                font-size: 12px;

                a {
                    color: #757575;
                }
            }
        }

        .links-contant {
            float: left;
            width: 239px;
            border-left: 1px solid #e0e0e0;
            text-align: center;
            color: #616161;

            .phone {
                font-size: 22px;
                line-height: 1;
                color: #ff6700;
                margin-bottom: 5px;
            }

            p:nth-child(2) {
                margin: 0 0 5px 0;
                font-size: 12px;
                margin-bottom: 5px;
            }

            .btn {
                display: inline-block;
                border: 1px solid #ff6700;
                text-align: center;
                transition: all .4s;
                width: 118px;
                height: 28px;
                font-size: 12px;
                line-height: 28px;
                background: #fff;
                color: #ff6700;

                .iconfont {
                    font-size: 13px;
                }

                &:hover {
                    background-color: #ff6700;
                    color: white;
                }
            }

            .follow {
                font-size: 12px;
                margin-top: 10px;

                a {
                    width: 24px;
                    height: 24px;
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 6px;
                    margin-top: -3px;
                }

                .wb {
                    background: url(https://cdn.cnbj1.fds.api.mi-img.com/staticsfile/global/wb.png) 50% 0 no-repeat;
                    background-size: cover;
                }

                .wx {
                    background: url(https://cdn.cnbj1.fds.api.mi-img.com/staticsfile/global/wx.png) 50% 0 no-repeat;
                    background-size: cover;
                }
            }
        }
    }
}

.site-info {
    padding: 30px 0;
    font-size: 12px;

    .container {
        position: relative;

        .logo {
            width: 56px;
            height: 56px;
            margin-right: 20px;
            background: url(https://s02.mifile.cn/assets/static/image/logo-mi2.png) no-repeat;
            background-size: 56px;
            position: absolute;
            top: 0;
            left: 0;
        }

        .sites {
            margin-left: 80px;
            line-height: 18px;

            a {
                font-size: 12px;
                color: #757575;
            }

            span {
                color: #b0b0b0;
                font-size: 12px;
            }
        }

        p {
            color: #b0b0b0;
            margin-left: 80px;

            a {
                font-size: 12px;
                color: #b0b0b0;

                &:hover {
                    color: #ff6700;
                }
            }
        }
    }

}